import React,{Component} from 'react';
import './UserDashBoard.css';
import Aux from '../../../hoc/AuxHoc';
import DashBoard from '../../DashBoard/DashBoard';
import Mape from '../../MapeDashBoard/Mape';
import {Link} from "react-router-dom";
import trackFluSensor from '../../../Apis/trackFluSensor';
import Cookies from 'universal-cookie';
import { connect } from 'react-redux';
import {UserSignIn,UserCurrent} from '../../../actions/index';
import 'antd/dist/antd.css';
import ReactLoading from 'react-loading';
import { Select } from 'antd';
import { io } from 'socket.io-client';
const { Option } = Select;

const cookies = new Cookies();


class UserDashBoard extends Component {
  constructor(props) {
    super(props);
    this.state={
      vehicles:null,
       options: [],
       defaultValue:null,
       vehicle_registration_number:null,
       vehicle_registration_number_Update:null
    }
    this.JWDauthorization= this.JWDauthorization.bind(this);
    this.handleChange= this.handleChange.bind(this);
    this.vehicleUpdate=this.vehicleUpdate.bind(this)

  }
   componentDidMount(){
    this.JWDauthorization();
    const socket = io("https://jayasinghes-fuel-sensor-server-2812.zeet.app", {
      serveClient: false,
      // below are engine.IO options
      origins: '*:*',
      transports: ['polling'],
    });
    console.log("socket on")
    socket.on("tx", data => {
      console.log(data)
      console.log("socket on")
       const previousState = this.state.vehicle_registration_number;
  

         if(previousState.vehicle_registration_number===data.vehicle_registration_number){
          previousState.locations=data.locations
          this.setState({vehicle_registration_number:previousState})
          this.setState({vehicle_registration_number_Update:true})
         }
         console.log(this.state.vehicle_registration_number)
    });
    
  }
  vehicleUpdate(){
    this.setState({vehicle_registration_number_Update:false})
  }
  async JWDauthorization(){
    var JWDUSER= null;
     JWDUSER = cookies.get('JWDUSER')

    if(JWDUSER){
    try{
  
      const config = {
        headers: { Authorization: `Bearer ${JWDUSER}` }
    };
      const response =  await trackFluSensor.get("/Users/me",config);
      await this.props.UserSignIn(response.data)
      await this.setState({vehicles:response.data.user.vehicles})
      for(var M = 0; M < this.state.vehicles.length; M++ ){
       await this.setState({ options: [...this.state.options, {"value":this.state.vehicles[M].vehicle_registration_number}] });
       await this.setState({defaultValue:this.state.vehicles[0].vehicle_registration_number})
      }
      var vehicle_registration_number = this.state.defaultValue
      const response_vehicle_registration_number = await trackFluSensor.post('/Track/vehiclReregistrationNumber', { vehicle_registration_number});
      await this.props.UserCurrent(response_vehicle_registration_number.data)
      await this.setState({vehicle_registration_number:response_vehicle_registration_number.data})
      this.setState({vehicle_registration_number_Update:true})
      console.log("socket on 9")
 
    }catch(e){
       console.log("error")
    }
  }
  }
async handleChange(value) {
    this.setState({vehicle_registration_number_Update:true})
     
    var vehicle_registration_number = value
    const response_vehicle_registration_number = await trackFluSensor.post('/Track/vehiclReregistrationNumber', { vehicle_registration_number});
    await this.props.UserCurrent(response_vehicle_registration_number.data)
    await this.setState({vehicle_registration_number:response_vehicle_registration_number.data})
    this.setState({vehicle_registration_number_Update:true})

  }


  

  render(){
    return(
     <Aux>
        {this.state.vehicle_registration_number?<div>
                <ul>
                    <li><Link className="Link"  to="/UserDashBoard">{this.props.userState.users.user.Name}</Link></li>
                    <li className="li"  style={{ float: "right"}}> vehicle registration number
                        <Select defaultValue={this.state.defaultValue}  bordered={false} style={{ width: 120 }} onChange={this.handleChange} options={this.state.options} />
                    </li>
                </ul>
                  <DashBoard values={this.state} vehicle_Update={this.vehicleUpdate}></DashBoard>
                  <Mape values={this.state} vehicle_Update={this.vehicleUpdate}/>
                
           </div>:<ReactLoading type={"bubbles"} color={"#375EE6"} height={200} width={200} />

        }
     </Aux>
      
    )
  }
}

const mapStateToProps = state => {
  return { userState: state.user };
};

export default connect(mapStateToProps,{UserSignIn,UserCurrent})(UserDashBoard);