import React from 'react';
import Aux from '../../hoc/AuxHoc';
import './CardsValue.css'; 

const CardsValue =(props)=>(

    <Aux>
        <div className = "card card-2">
        <div className="flex-container">
            <div>
            <h4>Date = {props.Date}</h4>  
            </div>
            <div>
                <h4>Time = {props.Time}</h4> 
            </div>
            <div>
            <h4>Fuel Level = {props.FuelLevel}</h4>  
            </div>  
            <div>
            <h4>Fuel Hight = {props.FuelHight}</h4>  
            </div>
 
 
        </div>

         </div>
    </Aux>
);

export default CardsValue;