import React,{ useState }  from 'react';
import Aux from '../../hoc/AuxHoc';
import './Cards.css'; 
import {
    RadialGauge
} from '@progress/kendo-react-gauges';

const gaugeStyles = {
    display: 'block',

    width:"300px"

};
const Card =(props)=>{

const radialOptions = {
    value: props.Level,
    shape: 'arrow',
    scale: {
        minorUnit: props.minorUnit,
        majorUnit: props.majorUnit,
        max: props.max,
        ranges: props.ranges
    }
};

   return <Aux>
        <div className = "card card-2">
     
            <div>
            <h4>{props.Name} = {props.Level}</h4>  
            <RadialGauge style={gaugeStyles} {...radialOptions}></RadialGauge>
            </div>

         </div>
    </Aux>
};

export default Card;