import React from 'react';
import Aux from '../../hoc/AuxHoc';
import './GoogleMaps.css';
import {withScriptjs ,withGoogleMap, GoogleMap, Marker } from "react-google-maps";
import 'antd/dist/antd.css';
import { connect } from "react-redux"; 
import {CarOutlined} from '@ant-design/icons';
const MyMapComponent  =(props)=>{
   function mapVehicleLoction(){
    if (props.track) { 
        return props.track.map(item =>{
            console.log(item.locations)
            return item.locations.map(v=>{
                console.log(v.Lat)
                return <Marker 
                        position={{ lat:v.Lat, lng:v.Lon}} 
                        label={item.vehicle_registration_number}
                        icon={ <CarOutlined />}
                         />
            })
        })
      }
    }
    return <Aux>
       <GoogleMap
            defaultZoom={8}
            defaultCenter={{ lat:  0, lng: 0 }}
        >
            {props.isMarkerShown && <Marker position={props.location} />}
            {props.isMarkerShownAll && mapVehicleLoction()}
        </GoogleMap>
    </Aux>
};
const mapStateToProps = state => {
    return { track: state.Track.data  };
  };
export default connect(mapStateToProps)(withScriptjs(withGoogleMap(MyMapComponent))) ;