import React from 'react';
import { Field, reduxForm  } from 'redux-form';
import validate from './validate';
import renderField from './renderField';
import{Link} from 'react-router-dom';
import './SignInpage.css';
const renderError = ({ meta: { touched, error } }) =>
  touched && error ? <span>{error}</span> : false;

const WizardFormSecondPage = props => {
  const { handleSubmit, pristine, submitting } = props;
  return (
    <form onSubmit={handleSubmit} className='form'>
      <Field
        name="email"
        type="email"
        component={renderField}
        label="email"
      />
      <Field
        name="password"
        type="password"
        component={renderField}
        label="password"
      />
       <Field name="email" component={renderError} />
      <div>
        <button type="submit" disabled={pristine || submitting}>Submit</button>
        <button type="submit" className="next"> <Link className="Link"  to="/SignUp">Sign In</Link> </button>
        <button type="submit" className="next"> <Link className="Link"  to="/UserSignIn">User Sign In</Link> </button>
      </div>
    </form>
  );
};

export default reduxForm({
  form: 'SignInUser', //                 <------ same form name
  destroyOnUnmount: false, //        <------ preserve form data
  forceUnregisterOnUnmount: true, // <------ unregister fields on unmount
  validate,
})(WizardFormSecondPage);
