import {
    TRACKING_DETAILS_USER,
  } from '../actions/types';

const INTIAL_STATE = {
vehicle_Tracking : null,
 
};

export default (state = INTIAL_STATE, action) => {
  switch (action.type) {
    case TRACKING_DETAILS_USER:
      return { ...state, vehicle_Tracking: action.payload };
    default:
      return state;
  }
};
