import React,{Component} from 'react';
import './userInformation.css';
import Aux from '../../../hoc/AuxHoc';
import Heder from "../../../components/Layout/heder";
import 'antd/dist/antd.css';
import {Link} from "react-router-dom";
import { Table, Tag, Space,Popconfirm } from 'antd';
import trackFluSensor from '../../../Apis/trackFluSensor';
import { Menu, Dropdown, Button } from 'antd';
import { Typography } from 'antd';
import ReactLoading from 'react-loading';
const { Text } = Typography;
class UserInformation extends Component {
  constructor(props) {
    super(props);
    this.state ={
      users:null
    }
    this.MenuIterable = this.MenuIterable.bind(this);
    this.deleteCompany = this.deleteCompany.bind(this);

  }
  async componentDidMount() {
    try{
      const response = await trackFluSensor.get("Users");
      this.setState({users:response.data})
      console.log(this.state.users)
   
    }catch(e){
    
        this.state.erorr ="user alredy save"
        console.log("www")

    }
  }
   MenuIterable(vehicles) {
    const menu = ( <Menu >
          {vehicles.map((vehicle) =>
              <Menu.Item key={vehicle._id}>
                  <a type="primary">{vehicle.vehicle_registration_number}</a>
              </ Menu.Item>
          )}
      </Menu>)
    return (
      <Dropdown overlay={menu} placement="bottomLeft">
      <Tag color={"geekblue"}>
                 vehicle registration number
              </Tag>
    </Dropdown>
    )
  }
  MenuIterablesensor_id(vehicles) {
    const menu2 = ( <Menu >
          {vehicles.map((vehicle) =>
              <Menu.Item key={vehicle._id}>
                  <a type="primary">{vehicle.sensor_id}</a>
              </ Menu.Item>
          )}
      </Menu>)
    return (
      <Dropdown overlay={menu2} placement="bottomLeft">
      <Tag color={"green"}>
               sensor id
              </Tag>
    </Dropdown>
    )
  }


  async deleteCompany(a){
    const response = await trackFluSensor.delete(`/Users/${a._id}`);
    console.log(response)
  
  }

  render(){ 
    
    const columns = [
      {
        title: 'Company Name',
        dataIndex: 'Name',
        key: 'Name',
        render: Name => <Text type="success">{Name}</Text>,
      },
      {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
        render: email => <Text type="warning">{email}</Text>,
      },
      {
        title: 'vehicles',
        key: 'vehicles',
        dataIndex: 'vehicles',
        render:vehicles => this.MenuIterable(vehicles),
      },
      {
        title: 'Sensor Id',
        key: 'sensor_id',
        dataIndex: 'vehicles',
        render:vehicles => this.MenuIterablesensor_id(vehicles),
      },
      {
        title: 'Action',
        key: 'action',
        render: (action) => (
          <Space size="middle">
             <Link to={`/UserInformationSubmit/${action._id}`}>update</Link>
            <Popconfirm title="Sure to delete?" onConfirm={() => this.deleteCompany(action)}>
              <a>Delete</a>
            </Popconfirm>
          </Space>
        ),
      },
    ];
    
  const data = this.state.users

    
    return(
      
     <Aux>
        {this.state.users? 
        <div>
            <Heder></Heder>
            <Link  style={{float:"right", marginRight:"5%"}} className="btn btn-gradient gradient2"  to="/UserInformationSubmit" >Add New User</Link>
            <Table columns={columns} dataSource={data}/>
      </div>
      :<ReactLoading type={"bubbles"} color={"#375EE6"} height={200} width={200} />}   
     </Aux>

    )
  }
}

export default UserInformation;