import React,{Component} from 'react';
import './App.css';
import {BrowserRouter} from 'react-router-dom';
import SignIn from '../src/containers/NavigationTab/SignInPage/SignInpage';
import SignUp from '../src/containers/NavigationTab/SignUpPage/SignUpPage';
import AdminDashBoard from '../src/containers/NavigationTab/AdminDashBoard/AdminDashBoard'; 
import UserDashBoard from '../src/containers/NavigationTab/UserDashBoard/UserDashBoard'; 
import UserInformation from '../src/containers/NavigationTab/UserInformation/userInformation';
import AdminInformation from '../src/containers/NavigationTab/AdminInformation/adminInformation';
import AdminInformationSubmit from '../src/containers/NavigationTab/AdminInformationSubmit/adminInformationSubmit';
import UserInformationSubmit from '../src/containers/NavigationTab/UserInformationSubmit/userInformationSubmit';
import UserSignIn from './containers/NavigationTab/UserSignUp/UserSignUp'
import {Route} from "react-router-dom";
class App extends Component {
  render(){
    return(

    <BrowserRouter>
        <div className="App">
         <Route path="/" exact component ={SignIn}></Route>
         <Route path="/SignUp" exact component ={SignUp}></Route>
         <Route path="/UserDashBoard" exact component ={UserDashBoard}></Route>
         <Route path="/AdminDashBoard" exact component ={AdminDashBoard}></Route>
        < Route path="/AdminInformation" exact component ={AdminInformation}></Route>
         <Route path="/UserInformation" exact component ={UserInformation}></Route>
         <Route path="/AdminInformationSubmit" exact component ={AdminInformationSubmit}></Route>
         <Route path="/UserInformationSubmit" exact component ={UserInformationSubmit}></Route>
         <Route path="/UserInformationSubmit/:id" exact component ={UserInformationSubmit}></Route>
         <Route path="/UserSignIn" exact component ={UserSignIn}></Route>
        </div>
    </BrowserRouter>
    
    )
  }
}

export default App;
