import trackFluSensor from '../Apis/trackFluSensor';
import {
  SIGN_IN_USER,
  SIGN_OUT_USER,
  TRACKING_DETAILS_USER,
  USER_CURRENT,
} from './types';

export const UserCurrent = userCurrent => {
  return {
    type: USER_CURRENT,
    payload: userCurrent
  };
};
export const trackingDetailsUser = track => {
  return {
    type: TRACKING_DETAILS_USER,
    payload: track
  };
};
export const UserSignIn = user => {
  return {
    type: SIGN_IN_USER,
    payload: user
  };
};


export const UserSignOut = () => {
  return {
    type: SIGN_OUT_USER
  };
};

export const fetchPostsTrack = () => async dispatch => {
  const response = await trackFluSensor.get('Track/New', { crossdomain: true });
console.log(response.data)
  dispatch({ type: 'FETCH_TRACK', payload: response });
};

export const userLoginStatus = (payload) => {
  return {
   type: 'USER_LOGIN',
   payload: payload
 };
 };
