import React,{Component} from 'react';
import './Table.css';
import Aux from '../../hoc/AuxHoc';
import { fetchPostsTrack,trackingDetailsUser } from '../../actions/index';
import trackFluSensor from '../../Apis/trackFluSensor';
import { connect } from 'react-redux';
import "antd/dist/antd.css";
import { Table, Tag, Space } from 'antd';
import { Menu, Dropdown, Button } from 'antd';
import { Typography } from 'antd';
import ReactLoading from 'react-loading';
import { io } from 'socket.io-client';
const { Text } = Typography;
// default values


class TableAdmin extends Component {
  constructor(props) {
    super(props);
    this.state = {
     post:[]

    };
 this.MenuIterable= this.MenuIterable.bind(this)

  }
   async componentDidMount() {
         await this.props.fetchPostsTrack();
         await this.setState({post:this.props.posts})
         const socket = io("https://jayasinghes-fuel-sensor-server-2812.zeet.app", {
          serveClient: false,
          // below are engine.IO options
          origins: '*:*',
          transports: ['polling'],
        });
        socket.on("tx", data => {
          console.log(data)
          
           const previousState = this.state.post;
           previousState.forEach((pState)=>{
             if(pState.vehicle_registration_number===data.vehicle_registration_number){
              pState.locations=data.locations
             }
           })
           this.setState({post:previousState})

        });
   }
 
      MenuIterable(locations,ss) {
        var c =null
         
        const menu = ( <Menu >
              {locations.map((location) =>{
                c = location[ss];
 
                return <Menu.Item key={location._id}>
                      <a type="primary">{c}</a>
                  </ Menu.Item>
      })}
          </Menu>)
        return (
          <Dropdown overlay={menu} placement="bottomLeft">
          <Tag color={"geekblue"}>
                     {ss +" = "+c}
                  </Tag>
        </Dropdown>
        )
      }
  render(){

    const columns = [
      {
        title: 'Company Name',
        width: 100,
        dataIndex: 'CompanyName',
        key: '_id',
        fixed: 'left',
        render: CompanyName => <Text type="success">{CompanyName}</Text>,
      },
      {
        title: 'Sensor ID',
        width: 100,
        dataIndex: 'SensorID',
        key: '_id',
        fixed: 'left',
      },
      { title: 'Year', dataIndex: 'locations', key: '2' ,width: 125,render:locations => this.MenuIterable(locations,"Year"),},
      { title: 'Month', dataIndex: 'locations', key: '3' ,width: 125,render:locations => this.MenuIterable(locations,"Month")},
      { title: 'Day', dataIndex: 'locations', key: '1' ,width: 125,render:locations => this.MenuIterable(locations,"Day")},
      { title: 'Hours', dataIndex: 'locations', key: '4' ,width: 125,render:locations => this.MenuIterable(locations,"Hours"),},
      { title: 'Minutes', dataIndex: 'locations', key: '5',width: 125,render:locations => this.MenuIterable(locations,"Minutes") },
      { title: 'latitude', dataIndex: 'locations', key: '6',width: 125,render:locations => this.MenuIterable(locations,"Lat")},
      { title: 'longitude', dataIndex: 'locations', key: '7',width: 125,render:locations => this.MenuIterable(locations,"Lon") },
      { title: 'Fuel Level', dataIndex: 'locations', key: '8',width: 125,render:locations => this.MenuIterable(locations,"FuelLevel") },
      { title: 'Fuel Hight', dataIndex: 'locations', key: '8',width: 125,render:locations => this.MenuIterable(locations,"FuelHight") },
      {
        title: 'vehicle registration number',
        key: '_id',
        dataIndex: 'vehicle_registration_number',
        fixed: 'right',
        width: 100,
      },
    ];
    const data = this.state.post
    return(
     <Aux>
          <Table columns={columns} dataSource={data} style={{width:800}} scroll={{ x: 500 , }}/>
     </Aux>
    )
  }
}

const mapStateToProps = state => {
    return { posts: state.Track.data };
  };
export default connect(
    mapStateToProps,
    { fetchPostsTrack ,trackingDetailsUser}
  )(TableAdmin);