import {  SIGN_IN_USER,SIGN_OUT_USER,USER_CURRENT} from '../actions/types';

const INTIAL_STATE = {
  isSignedIn: null,
  users:null,
  user:null
};

export default (state = INTIAL_STATE, action) => {
  switch (action.type) {
    case SIGN_IN_USER:
      return { ...state, isSignedIn: true, users: action.payload };
    case SIGN_OUT_USER:
      return { ...state, isSignedIn: false, userId: null };
    case USER_CURRENT:
      return { ...state, user: action.payload };
    default:
      return state;
  }
};
