import React,{Component} from 'react';
import './SignUpPage.css';
import WizardForm from "./WizardForm";
import { connect } from 'react-redux';
import trackFluSensor from '../../../Apis/trackFluSensor';
import {userLoginStatus} from '../../../actions/index';
import Cookies from 'universal-cookie';
const cookies = new Cookies();
 
class SignUp extends Component {
  constructor(props) {
    super(props);
    this.state={
      
    }
    this.onSubmit = this.onSubmit.bind(this);
    this.JWDauthorization= this.JWDauthorization.bind(this);
  }
  async onSubmit(formValues) {
    const Email = formValues.email
    const PassWord = formValues.password
    const Name = formValues.Name

    const response = await trackFluSensor.post("Admins",{Name, PassWord,Email});
  
    cookies.set('JWD', response.data.token, { path: '/' });
    this.props.userLoginStatus(response.data)
    this.props.history.push('/AdminDashBoard');
  }
async JWDauthorization(){
  try{
    const JWD = cookies.get('JWD')
    const config = {
      headers: { Authorization: `Bearer ${JWD}` }
  };
    console.log(JWD)
    if (JWD == null){
      return
  }
    const response =  await trackFluSensor.get("/Admins/me",config);
    const JWDStatus = response.data.admintatus
    this.props.userLoginStatus(response.data)
    if(JWDStatus){
      this.props.history.push('/AdminDashBoard');
    }
    console.log(JWDStatus)   
  }catch(e){
     console.log("error")
  }
    
}
  render(){
      this.JWDauthorization();
  return(
      <WizardForm onSubmit={this.onSubmit} />
	);
  }
}
const mapStateToProps = state => {
  return { AdminState: state };
};

export default connect(mapStateToProps,{userLoginStatus})(SignUp);