const initialState = {
  userLoginStatus: []
};
  
const  AdminState= (state=initialState, action) => {
    switch (action.type) {
      case "USER_LOGIN":
        return Object.assign({}, state, {
          userLoginStatus: state.userLoginStatus.concat(action.payload),
        });
      default:
        return state;
    }
  };
  export default  AdminState