import React,{Component} from 'react';
import './UserSignUp.css';
import WizardForm from "./WizardForm";
import { connect } from 'react-redux';
import {UserSignIn} from '../../../actions/index';
import trackFluSensor from '../../../Apis/trackFluSensor';
import Cookies from 'universal-cookie';


const cookies = new Cookies();
class UsersSignIn extends Component {
  constructor(props) {
    super(props);
    this.onSubmit = this.onSubmit.bind(this);
    this.JWDauthorization= this.JWDauthorization.bind(this);
    
   
  }
  async onSubmit(formValues) {
 
    const Email = formValues.email
    const PassWord = formValues.password
    const response = await trackFluSensor.post("/User/LogIn",{PassWord,Email});
  
    cookies.set('JWDUSER', response.data.token, { path: '/' });
    this.props.UserSignIn(response.data)
    this.props.history.push('/UserDashBoard')
  }


  async JWDauthorization(){
    try{
      const JWDUSER = cookies.get('JWDUSER')
      const config = {
        headers: { Authorization: `Bearer ${JWDUSER}` }
    };
      const response =  await trackFluSensor.get("/Users/me",config);
      const JWDStatus = response.data.userStatus
      this.props.UserSignIn(response.data)
      if(JWDStatus){
        this.props.history.push('/UserDashBoard');
      }

    }catch(e){
       console.log("error")
    }
      
  }

  render(){
    this.JWDauthorization();
  return(

      <WizardForm onSubmit={this.onSubmit} />

	);
  }
}
const mapStateToProps = state => {
  return { userState: state.user };
};

export default connect(mapStateToProps,{UserSignIn})(UsersSignIn);