import React,{Component} from 'react';
import './adminInformation.css';
import Aux from '../../../hoc/AuxHoc';
import Heder from "../../../components/Layout/heder";
import 'antd/dist/antd.css';
import { Table, Tag, Space,Popconfirm } from 'antd';
import 'antd/dist/antd.css';
import trackFluSensor from '../../../Apis/trackFluSensor';
import ReactLoading from 'react-loading';
import { Typography } from 'antd';
const { Text } = Typography;
class AdminInformation extends Component {
  constructor(props) {
    super(props);
    this.state ={
      admin:null
    }
  this.deleteCompany = this.deleteCompany.bind(this);
  this.updateCompany = this.updateCompany.bind(this);
  }
  async componentDidMount() {
    try{
      const response = await trackFluSensor.get("Admins");
      this.setState({users:response.data})
      console.log(this.state.users)
   
    }catch(e){
    
        this.state.erorr ="user alredy save"
        console.log("www")

    }
  }
  async deleteCompany(a){
    console.log(a)
  }

  async updateCompany(a){
    console.log(a)
  }
  render(){
    
    const columns = [
      {
        title: 'Admin Name',
        dataIndex: 'Name',
        key: 'Name',
        render: Name => <Text type="success">{Name}</Text>,
      },
      {
        title: 'Admin Email',
        dataIndex: 'Email',
        key: 'Email',
        render: Email => <Text type="warning">{Email}</Text>,
      },
      {
        title: 'Tags',
        key: 'tags',
        dataIndex: 'tags',
        render: tags => <Tag color="#DC143C"> Developer</Tag>
      },
      {
        title: 'Action',
        key: 'action',
        render: (action) => (
          <Space size="middle">
            <Popconfirm title="Sure to update?" onConfirm={() => this.updateCompany(action)}>
              <a href="#">update</a>
            </Popconfirm>
            <Popconfirm title="Sure to delete?" onConfirm={() => this.deleteCompany(action)}>
              <a href="#">Delete</a>
            </Popconfirm>
          </Space>
        ),
      },
    
    ];

    const data = this.state.users

    return(
      <Aux>
      {this.state.users? 
        <div>
            <Heder></Heder>
            <Table columns={columns} dataSource={data}/>
      </div>
      :<ReactLoading type={"bubbles"} color={"#375EE6"} height={200} width={200} />}  
      </Aux>
    )
  }
}

export default AdminInformation;