import React from 'react';
import { Field, reduxForm  } from 'redux-form';
import { Table , Button } from 'antd';
import validate from './validate';
import renderField from './renderField';
import {  Popconfirm  } from 'antd';
import "antd/dist/antd.css";
import { connect } from 'react-redux';
import trackFluSensor from '../../../Apis/trackFluSensor';
import { withRouter } from 'react-router-dom';
class WizardFormSecondPage extends React.Component{
  constructor(props) {
    super(props);
   this.state={
    Vehicles_Information:[],
    vehicle_registration_number_state:null,
    sensor_id_state:null,
    Vehicle_state:false,
    erorr :"",
    updateRequest:false,
    id:null
   }
   this.addVehicles_Information = this.addVehicles_Information.bind(this);
   this.handleDelete = this.handleDelete.bind(this);
   this.onSubmit=this.onSubmit.bind(this);
   this.updateForme= this.updateForme.bind(this);
  }
  componentDidMount(){
    if(this.props.match.params.id){
      this.updateForme(this.props.match.params.id)
      this.state.updateRequest=true
      }else{
      this.state.updateRequest=false
      }

  }
  componentDidUpdate(prevProps, prevState, snapshot){
    if(this.props.formeUser.values===undefined){
      this.state.Vehicle_state=false
 

    }else{
      if(this.props.formeUser.values.vehicle_registration_number && this.props.formeUser.values.sensor_id){
         this.state.Vehicle_state=true

        }
        if(this.props.formeUser.values.vehicle_registration_number===undefined){
          this.state.Vehicle_state=false

         }
         
         if(this.props.formeUser.values.sensor_id===undefined){
          this.state.Vehicle_state=false
         
         }
    }
   
  }
  async addVehicles_Information() {
    let key = Math.floor(Math.random() * 1000000000000);

    let newVehicles = {
      key: key,
      vehicle_registration_number:this.props.formeUser.values.vehicle_registration_number,
      sensor_id:this.props.formeUser.values.sensor_id
    };
    console.log(newVehicles)
   await this.setState({ Vehicles_Information: [...this.state.Vehicles_Information, newVehicles] });
  }
  handleDelete(key) {
    const Vehicles = [...this.state.Vehicles_Information];
    this.setState({ Vehicles_Information: Vehicles.filter(item => item.key !== key) });
  }
  async onSubmit(formValues){
    if(!this.state.updateRequest){
      const Name = formValues.Name
      const email = formValues.email
      const PassWord = formValues.password
      const vehicles = this.state.Vehicles_Information
    try{
      const response = await trackFluSensor.post("Users",{Name,email, PassWord,vehicles});
      this.props.history.push('/UserInformation');

      console.log(response)
    }catch(e){
    
        this.state.erorr ="user alredy save"
        console.log("www")

    }
  }
  if(this.state.updateRequest){
    const Name = formValues.Name
    const email = formValues.email
    const PassWord = formValues.password
    const vehicles = this.state.Vehicles_Information

    try{
      const response = await trackFluSensor.patch(`/Users/${this.state.id}`,{Name,email, PassWord,vehicles});
      this.props.history.push('/UserInformation');

      console.log(response)
    }catch(e){
    
        this.state.erorr ="user alredy save"
        console.log("www")

    }
  }
  
}
 async updateForme(id){
  const updateResponse = await trackFluSensor.get(`Users/${id}`)
  await this.setState({id:id})
  this.props.initialize({ Name: updateResponse.data.Name ,email: updateResponse.data.email});
  for(let r = 0 ; r < updateResponse.data.vehicles.length; r++ ){
    await this.setState({ Vehicles_Information: [...this.state.Vehicles_Information, updateResponse.data.vehicles[r] ] });    
  }
  console.log(updateResponse.data)
 }
render(){
  const columns = [
    {
      title: 'vehicle registration number',
      dataIndex: 'vehicle_registration_number',
      key: 'vehicle_registration_number',
      className:"m-0 p-1",

    },
    {
      title: 'sensor id',
      dataIndex: 'sensor_id',
      key: 'sensor_id',
      className:"m-0 p-1",
 
    },
    {
      title: 'Action',
      dataIndex: '',
      key: 'x',
      width: 100,
      fixed: 'right',
      render: (text, record) =>
        this.state.Vehicles_Information.length >= 1 ? (
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <a href="#">Delete</a>
          </Popconfirm>
        ) : null,
    },
  ];
  return (
    <form onSubmit={this.props.handleSubmit(this.onSubmit)}>
      <h3>Vehicles Information</h3>
      <div className="form-inline">
            <Field
              name="vehicle_registration_number"
              type="text"
              component={renderField}
              label="Vehicle Registration Number"
              autocomplete="off"

            />
              <Field
              name="sensor_id"
              type="text"
              component={renderField}
              label="Sensor Id"
              autocomplete="off"
            />
      </div>
      <Button disabled={!this.state.Vehicle_state} onClick={() => this.addVehicles_Information()} className="btn btn-gradient gradient2">Add Vehicle</Button>
      <h3>Company Information</h3>
      <div className="form-inline">
          <Field
            name="Name"
            type="text"
            component={renderField}
            label="Company Name"
          />
          <Field
            name="email"
            type="email"
            component={renderField}
            label=" Company Email"
            autocomplete="off"
          />
      </div>
      <div className="form-inline">
              <Field
                name="password"
                type="password"
                component={renderField}
                label="password"
                autocomplete="off"
              />
                <Field
                name="repassword"
                type="password"
                component={renderField}
                label="Reenter password"
                autocomplete="off"
              />
      </div>
      
      <div >
        <button   className="btn btn-gradient gradient2 form-inline-button">Submit</button>
      </div>
       <div style={{color:"#ff0000"}} >{this.state.erorr}</div>
      <Table
            dataSource={this.state.Vehicles_Information}
            columns={columns}
            pagination={false}
            summary={pageData => {
              let totalCartons = 0;
 
              pageData.forEach(({ num }) => {
                totalCartons += num;
              });
 
              return (
                <tr>
                  <td>

                  </td>
                </tr>
              );
            }}
          />
    </form>
    
  );
};
}

const UserInformationForm= reduxForm({
  form: 'UserInformation', //                 <------ same form name
  destroyOnUnmount: false, //        <------ preserve form data
  forceUnregisterOnUnmount: true, // <------ unregister fields on unmount
  validate,
})(WizardFormSecondPage);

const mapStateToProps = state => {
  return { formeUser: state.form.UserInformation };
};
const ShowTheLocationWithRouter = withRouter(UserInformationForm);
export default connect(mapStateToProps)(ShowTheLocationWithRouter);